"use client";

import React, { useEffect, useState } from "react";
import {
  Box,
  Flex,
  Text,
  Avatar,
  HStack,
  VStack,
  Divider,
  Textarea,
  IconButton,
  useColorModeValue,
  TagLabel,
  Tag,
} from "@chakra-ui/react";
import { FaGlobe, FaInstagram, FaSnapchatGhost, FaTiktok } from "react-icons/fa";
import placeholderAvatar from "../../data/image/no-avatar.png";
import useColorModeStyles from "../../utils/useColorModeStyles";

interface UserData {
  image_url: string;
  first_name: string;
  last_name: string;
  age: number | null;
  gender: string;
  location: string;
  bio: string;
  views: number;
  likes: number;
  points: number;
  interests: string[];
  instagram?: string | null;
  snapchat?: string | null;
  tiktok?: string | null;
}

interface MainProfileProps {
  username: string; // Expecting the username as a prop
}

const MainProfile = ({ username }: MainProfileProps) => {
  const { bg, tiktok, textColor, borderColor, navBgColor } = useColorModeStyles();

  const [userData, setUserData] = useState<UserData>({
    image_url: placeholderAvatar.src,
    first_name: "Loading...",
    last_name: "",
    age: null,
    gender: "",
    location: "",
    bio: "",
    views: 0,
    likes: 0,
    points: 0,
    interests: [],
    instagram: null,
    snapchat: null,
    tiktok: null,
  });

  const [imagePreview, setImagePreview] = useState<string>(placeholderAvatar.src);

  useEffect(() => {
    const fetchUserData = async () => {
      try {
        const response = await fetch(`http://localhost:8000/api/v1/users/${username}/`);
        if (!response.ok) {
          throw new Error("Failed to fetch user data");
        }
        const data = await response.json();
        setUserData(data);
        setImagePreview(data.image_url || placeholderAvatar.src);
      } catch (error) {
        console.error("Failed to fetch user data:", error);
      }
    };

    if (username) {
      fetchUserData();
    }
  }, [username]);

  return (
    <Flex justify="center" align="center" py={5} px={5}>
      <Box
        maxW="lg"
        w="full"
        bg={bg}
        borderRadius="lg"
        p={8}
        boxShadow="lg"
        textAlign="center"
        borderColor={borderColor}
        borderWidth="1px"
      >
        {/* Avatar */}
        <Flex justify="center" mb={2}>
          <Avatar
            size="2xl"
            src={imagePreview}
            borderWidth="4px"
            borderColor={bg}
            boxShadow="lg"
          />
        </Flex>

        {/* Name and Age */}
        <Text fontSize="2xl" fontWeight="bold" color={textColor}>
          {userData.first_name} {userData.last_name}
          {userData.age !== null ? `, ${userData.age}` : ""}
        </Text>

        {/* Location */}
        <HStack justify="center" color={textColor} mb={2}>
          <FaGlobe />
          <Text>{userData.location || "Location not specified"}</Text>
        </HStack>

        <Divider borderColor={borderColor} mb={2} />

        {/* Social Media Icons */}
        <HStack justify="center" spacing={4} mb={2}>
          {userData.instagram && (
            <IconButton
              aria-label="Instagram"
              icon={<FaInstagram />}
              onClick={() => window.open(userData.instagram!, "_blank")}
              colorScheme="pink"
              variant="ghost"
            />
          )}
          {userData.snapchat && (
            <IconButton
              aria-label="Snapchat"
              icon={<FaSnapchatGhost />}
              onClick={() => window.open(userData.snapchat!, "_blank")}
              colorScheme="yellow"
              variant="ghost"
            />
          )}
          {userData.tiktok && (
            <IconButton
              aria-label="TikTok"
              icon={<FaTiktok />}
              onClick={() => window.open(userData.tiktok!, "_blank")}
              color={tiktok}
              bg="transparent"
              _hover={{
                bg: useColorModeValue("gray.200", "gray.700"),
              }}
              variant="ghost"
            />
          )}
        </HStack>

        <Divider borderColor={borderColor} mb={2} />

        {/* Stats */}
        <HStack justify="space-around" mb={2}>
          <VStack>
            <Text fontSize="lg" fontWeight="bold" color={textColor}>
              {userData.views}
            </Text>
            <Text>Views</Text>
          </VStack>
          <VStack>
            <Text fontSize="lg" fontWeight="bold" color={textColor}>
              {userData.likes}
            </Text>
            <Text>Likes</Text>
          </VStack>
          <VStack>
            <Text fontSize="lg" fontWeight="bold" color={textColor}>
              {userData.points}
            </Text>
            <Text>Points</Text>
          </VStack>
        </HStack>

        <Divider borderColor={borderColor} mb={2} />

        {/* Interests */}
        <Text fontSize="lg" fontWeight="bold" color={textColor} mb={2}>
          Interests
        </Text>
        <HStack spacing={2} wrap="wrap" justify="center" mb={2}>
          {userData.interests.length > 0 ? (
            userData.interests.map((tag, index) => (
              <Tag key={index} size="md" variant="subtle" colorScheme="teal">
                <TagLabel>{tag}</TagLabel>
              </Tag>
            ))
          ) : (
            <Text>No interests specified</Text>
          )}
        </HStack>

        <Divider borderColor={borderColor} mb={2} />

        {/* About Me */}
        <Text fontSize="lg" fontWeight="bold" color={textColor} mb={2}>
          About Me
        </Text>
        <Textarea
          value={userData.bio || "No bio available."}
          isReadOnly
          rows={3}
          resize="none"
          bg={navBgColor}
          color={textColor}
        />
      </Box>
    </Flex>
  );
};

export default MainProfile;
